import React, { useState } from 'react';
import PropTypes from 'prop-types';

import shortid from 'shortid';
import s from './ContactForm.module.css';

const initialState = {
  id: shortid.generate(),
  name: '',
  phoneNumber: '',
  isOpen: false,
};

const ContactForm = ({ contacts, onSubmit }) => {
  const [state, setState] = useState(initialState);
  const { name, phoneNumber, isOpen } = state;

  const handleSubmit = e => {
    e.preventDefault();

    const newContact = {
      id: shortid.generate(),
      name,
      phoneNumber,
    };

    const checkName = contacts.find(
      contact => contact.name === newContact.name,
    );

    if (checkName) {
      alert(`Contact witn name ${name} already exists`);
      setState(initialState);
      return;
    }
    onSubmit(newContact);
    setState(initialState);
  };

  const handleChange = e => {
    setState(prev => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const toggleModal = () => {
    setState(({ isOpen }) => ({
      isOpen: !isOpen,
    }));
  };

  return (
    <>
      <button className={s.btn} onClick={toggleModal}>
        Add contact
      </button>
      {isOpen && (
        <div className={s.modal}>
          <div className={s.modalBody}>
            <form onSubmit={handleSubmit} className={s.form}>
              <label className={s.label}>
                <span>Name:</span>
                <input
                  className={s.input}
                  type="text"
                  name="name"
                  value={name}
                  pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
                  title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
                  required
                  onChange={handleChange}
                />
              </label>

              <label>
                <span>Phone number:</span>
                <input
                  className={s.input}
                  type="tel"
                  name="phoneNumber"
                  value={phoneNumber}
                  pattern="(\+?( |-|\.)?\d{1,2}( |-|\.)?)?(\(?\d{3}\)?|\d{3})( |-|\.)?(\d{3}( |-|\.)?\d{4})"
                  title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –∏–∑ 11-12 —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ü–∏—Ñ—Ä—ã, –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –ø—É–∑–∞—Ç—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
                  required
                  onChange={handleChange}
                />
              </label>
              <div className={s.buttons}>
                <button className={s.btn} type="submit">
                  Add contact
                </button>
                <button className={s.btn} onClick={toggleModal}>
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </>
  );
};

ContactForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  contacts: PropTypes.array.isRequired,
};

export default ContactForm;
